<template>
    <div class="create-folder" @click="createStart">
        <div class="create-folder__icon">
            ‚ûï
        </div>
        <div class="create-folder__text">
            –î–æ–±–∞–≤–∏—Ç—å –ø–∞–ø–∫—É
        </div>
    </div>
</template>

<script>
    import { mapState, mapMutations } from 'vuex';
    import mixins from "../mixins";


    export default {
        name: "CreateFolder",
        mixins: [mixins],
        methods: {
            ...mapMutations(["setCurrentList"]),
            createStart() {
                let nameprompt = prompt("–í–≤–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ –ø–∞–ø–∫–∏", "–ù–æ–≤–∞—è –ø–∞–ø–∫–∞");
                if (nameprompt) {
                    let formData = new FormData();
                    formData.append('action', 'create',);
                    formData.append('current', this.currentPath);
                    formData.append("foldername", nameprompt);
                    this.request(this.baseURL, 'POST', formData).then((data) => {
                        this.setCurrentList(Object.values(data));
                    }).catch((message) => {
                        alert(message);
                    });
                }
            }
        },
        computed: {
            ...mapState(["currentPath", "baseURL"]),
        }
    }
</script>

<style scoped lang="stylus">
    .create-folder
        display flex
        align-items center
        cursor pointer
        font-size 20px
        margin-top 30px
        padding-left 10px
        input
            display none
        label
            cursor pointer
        &__icon
            margin-right 10px
</style>